.profile-header{
    height: 90px;
    background-color: #57068C;
    border-radius: 8px 8px 0 0;
}
.profile-header .profile-img{
    float: left;
    margin: 20px 0 0 7px;
    border-radius: 50%;
    overflow: hidden;
    border: 1px solid white;
}

.profile-header .profile-img img{
    width: 51px;
}
.profile-header .user-name{
    display: inline-block;
    margin: 28px 0 0 10px;
    font-size: 12px;
    color: white;
}
.profile-header .user-email{
    display: inline-block;
    margin: 10px 0 0 10px;
    font-size:8px;
    color: white;
}
.profile-header .change-info{
    background-color: #57068C;
    float: right;
    margin: 23px 5px 0 0;
    width: 20px;
    cursor: pointer;
}
