.edit-profile-main .edit-profile-goback{
    height: 40px;
    line-height: 40px;
    overflow: hidden;
}
.edit-profile-main .edit-profile-goback span{
    margin-left: 10px;
    font: 14px "Roboto";
}
.edit-profile-main .edit-profile-goback img{
    margin-right: 10px;
    float: right;
    width: 28px;
    margin-top: 6px;
    cursor: pointer;
}

.edit-profile-main .edit-grade{
    margin-top: 10px;
}
.edit-profile-main .edit-grade img{
    float: left;
    width: 20px;
    margin-left: 10px;
}
.edit-profile-main .edit-grade span{
    font: 10px "Roboto";
    margin-left: 10px;
}
.edit-profile-main .edit-grade input{
    width: 130px;
    height: 10px;
    font-size: 10px;
    border: 1px solid rgba(0, 0, 0, 0.5);
    border-radius: 4px;
    padding: 4px 7px;
    margin: 3px 0 0 10px;
}
.edit-profile-main .edit-grade input:focus{
    outline: none;
    border-color: rgba(82, 168, 236, 0.8);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 1px rgba(111, 0, 255, 0.6);
}

