.btn{
    display: inline-block;
    padding: 4px 12px;
    margin-bottom: 0;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.05);
    border-radius: 4px;
}
.btn:focus {
    outline: none;
}
.edit-profile-main .profile-save-button button{
    color: #fff;
    background-color: #BF3075;
    border: 1px solid #BF3075;
    margin-left: 57px;
}
.edit-profile-main .profile-save-button button:hover {
    color: #fff;
