.profile-main .grade-year-box{
    height: 40px;
    line-height: 40px;
    margin:  10px 0 0 8px;
    overflow: hidden;
}
.profile-main .grade-year-box img{
    float: left;
    margin-top: 6px;
    width: 30px;
}
.profile-main .grade-year-box span{
    display: inline-block;
    font-size: 10px;
}
.profile-main .grade-year-box .grade-1{
    margin-left: 5px;
}
.profile-main .grade-year-box .grade-2{
    margin-left: 10px;
}

.profile-main .class-box{
    height: 40px;
    line-height: 40px;
    margin-left: 10px;
    overflow: hidden;
}
.profile-main .class-box img{
    float: left;
    width: 23px;
    margin-top: 9px;
}
.profile-main .class-box span{
    display: inline-block;
    font-size: 10px;
}
.profile-main .class-box .class-1{
    margin-left: 10px;
}
.profile-main .class-box .class-2{
    margin-left: 10px;
}

.profile-main .major-box{
    height: 40px;
    line-height: 40px;
    margin-left: 8px;
    overflow: hidden;
}
.profile-main .major-box img{
    float: left;
    width: 26px;
    margin-top: 8px;
}
.profile-main .major-box span{
    display: inline-block;
    font-size: 10px;
}
.profile-main .major-box .major-1{
    margin-left: 8px;
}
.profile-main .major-box .major-2{
    margin-left: 10px;
}

.profile-main .bio-box{
    height: 40px;
    margin-top: 15px;
    overflow: hidden;
}
.profile-main .bio-box span{
    display: inline-block;
    margin: 10px 0 0 20px;
    font-size: 10px;
    color: rgba(0, 0, 0, 0.7);
    border-bottom: 1px solid rgba(0, 0, 0, 0.7);
    padding-bottom: 3px;
}